html {
  padding: 0;
  margin: 0;
  
}

p {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
    line-height: 30px;
    font-weight: 400;
    color: #fff;
    padding: 25px 0 25px 0;
    
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 60px;
  color: rgb(54, 52, 52);
}

h2{
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  letter-spacing: -2px;
  font-size: 30px;
  line-height: 60px;
  color: aliceblue;
  margin: 0;
}

h4 {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
}

h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 30px;
  color: #e3ccd1;
  letter-spacing: 5px;
  
  padding-left: 5px;
  padding-bottom: 10px;
  margin: 0;
}

