.container{
   
    box-sizing: border-box;
    font-family: 'Work Sans', sans-serif;
    font-weight: 300;
    font-size: large;
    padding: 0;
    margin: 0;
    width: 100vw;
    position: fixed;
    top: 0;
    z-index: 100;
}
.transparent {
    background: transparent;
    transition: background .3s;
    color: rgb(54, 52, 52);
}
.solid {
    background: #524e5e;
    transition: background .3s;
    color: aliceblue;
}

.navBar {
    display: flex;
    align-items: center;
    margin: 0 7%;
    height: 80px;

}
.logo {
    font-family: 'Montserrat', sans-serif;
    background-color: transparent;
    font-size: 30px;
    text-decoration: none;
    list-style-type: none;
    
    letter-spacing: 0.03cm;
    
}
.logo :hover {
    cursor: pointer;
}

.navBar ul {
    display: flex;
    justify-content: space-around;
    
    width: 40%;
    margin-left: auto;
    list-style-type: none;
    

}

.checkbox {
    
    font-size: 1.7rem;
    cursor: pointer;
    display: none;
    padding-right: 4%;
}



.links {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 40px;
    background-color: transparent;
    text-transform: uppercase;
    letter-spacing: 0.03cm;
    text-decoration: none;
}

.links:hover{
    color: aliceblue;
}


.fade-enter {
    opacity: 0;
    z-index: 1;
  }
  
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
  


@media (max-width: 952px) {
    .navBar h1 {
        font-size: 33px;
    }
    .links {
        font-size: 17px;
    }
    .navBar ul li{
        padding-right: 20%;
    }
}

@media (max-width: 858px) {
    .container{
        
    }
    .navBar .unchecked, .navBar .checked {
        position: fixed;
        
        width: 100vw;
        min-height: 100%;
        background: #524e5e;
        top: 60px;
        overflow: hidden;
        flex-direction: column;
        z-index: 100;
        justify-content: flex-start;
        transition: all .5s;
    }
    .unchecked {
        left: -120%;
    }
    .checked{
        left: 0;
    }
    .checkbox{
        display: inline-block;  
    }

    .navBar ul li {
        padding: 5%;
        
    }
    .links{
        font-size: 2rem;
        color: aliceblue;
    }
    
    

}