.body {
    display: flex;
    justify-content: space-between;
    height: 75vh;
    align-items: center;
    background-color: #615c6f;
    padding: 5% 0;

}

.section-color-2{
    background-color: #6a6579;
}

.body-text-container{
    display: flex;
    flex-direction: column;
    width: 35%;
    padding-left: 5%;
    margin-top: 3%;
}

.image{
    position: relative;
    background-image: url('./SleekWebsite.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    margin: 1%;
    border-radius: 5%;
    transition: all .2s ease-in;
    background-position: center;
}

.image:hover {
    opacity: .7;
    transform: scale(1.03);
    cursor: pointer;
}

.image-mobile{
    position: absolute;
    background-image: url('./SleekWebsitePhone.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 20%;
    height: 60%;
    border-radius: 10%;
    bottom: -5%;
    margin: 0;
    right: 1%;
    

}

.image-container{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 55%;
    height: 80%;
    margin: 1%;
}
.app-image{
    background-image: url('./gig-app.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 100%;
    width: 45%;
    border-radius: 5%;
    transition: all .2s ease-in;

}
.app-image:hover {
    opacity: .7;
    transform: scale(1.03);
    cursor: pointer;
}
.jamming-image{
    background-image: url('./Jamming.jpg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    border-radius: 5%;
    transition: all .2s ease-in;

}
.jamming-image:hover {
    opacity: .7;
    transform: scale(1.03);
    cursor: pointer;
}
.wordpress-image{
    background-image: url('wordpress.jpg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    border-radius: 5%;
    transition: all .2s ease-in;
}
.wordpress-image:hover {
    opacity: .7;
    transform: scale(1.03);
    cursor: pointer;
}

.button-link {
    color: #fff;
    display: inline-block;
    border: 2px solid #fff;
    text-align: center;
    padding: 12px 25px;
    cursor: pointer;
    margin-top: 0;
    vertical-align: middle;
    text-decoration: none;
    background: transparent;
    transition: all .2s linear;
    width: 30%;
  
}

.button-link:hover {
    color: #615c6f;
    background-color: #fff;
}

.stack {
    padding: 0;
}
.last-section{
    
}



@media(max-width: 1425px) {
    .button-link {
        width: 50%;
        padding: 8px 15px;
    }
    .stack {
        margin: 0;
    }
}
@media(max-width: 1050px) {
    .body{
        flex-direction: column;
        
    }
    .image-mobile{
        right: 15%;
    }
    .body-text-container{
        margin: 2% 0;
        width: 90%;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 0;
        
    }
    .description {
        width: 40%;

    }
    .improvements {
        padding-top: 1.3%;
        width: 40%;
        height: 20%;
    }
    .button-link{
        width: 100%;
    }
    .stack{
        text-align: center;
    }
    .image-container{
        width: 90%;
    height: 80%;
    
    }
    
}
.last-section .description {
    
    
    
}


@media(max-width: 800px) {
    .body{
        height: 80vh;
    }
    .body-text-container{
        width: 95%;
    }
    .image-mobile{
        bottom: 0px;
        right:5%;
        width: 15%;
    }
    .last-section .description {
        width:40%;
        text-align: center;
        
        
    }
}

@media(max-width: 650px) {
    .body{
        height: 115vh;
    }
    .body-text-container{
        width: 99%;
        flex-direction: column;
        
        align-items: center;
        justify-content: start;
        padding-top:5%;
        

    }
    .image-mobile{
        display: none;
    }
   
    .description{
        width: 95%;
        padding: 5% 0;
    }
    .improvements{
        width: 95%;
        padding: 6% 0;
    }
    .button-link{
        width: 90%;
    }
 
    .image{
        background-size: cover;
    }

    .app-image{
        background-size: cover;
    }

    .jamming-image{
        background-size: cover;
    }
    .wordpress-image{
        background-size: 130%;
    }
    .last-section{
        height: 80vh
    }
    .last-section .description{
        text-align: left;
        width: 95%
    }
}

@media(max-height: 750px) {
    .body{
        height: 155vh;
    }
    .last-section{
        height: 100vh
    }
}

@media(max-height: 650px) {
    .body{
        height: 170vh;
    }
    .image-mobile{
        display: none;
    }
    .last-section{
        height: 110vh
    }
}

@media(max-height: 570px) {
    .last-section{
        height: 130vh
    }
}
